import { useParams } from 'react-router-dom'
import { useState, useRef, useEffect, RefObject } from 'react'

import { 
  ChevronDownIcon,
  CloseIcon,
  RepeatIcon,
  LinkIcon,
  ArrowBackIcon
} from '@chakra-ui/icons'

import {
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Button, 
  Box,
  Heading,
  Wrap, 
  WrapItem,
  Spacer,
  Divider,
  Center,
  useToast,
  useClipboard
} from '@chakra-ui/react'
import { useNavigate, NavigateFunction } from "react-router-dom";

import Config from '../Config'
import Header from '../components/Layouts/Header'
import Footer from '../components/Layouts/Footer'
import CameraCard from '../components/Items/CameraCard'
import QRLinkModalButton from '../components/Items/QRLinkModalButton'

import Peer, {MeshRoom} from 'skyway-js'

type CameraStream = {
  stream: MediaStream;
  peerId: string;
};

const Dashboard = () => {
  const peer = useRef(new Peer({ key: Config().SKYWAY_API_KEY }));
  const params = useParams()
  const [roomId] = useState<string>(params.roomId || '')
  const [remoteVideo, setRemoteVideo] = useState<CameraStream[]>([]);
  const { hasCopied, onCopy } = useClipboard(window.location.href)
  const navigate = useNavigate();
  const toast = useToast()

  const onStartStream = () => {
    try {
      const room = peer.current.joinRoom(roomId, {
        mode: 'mesh'
      })

      room.once('open', () => {
        toast.closeAll()
        toast({
          position: 'bottom',
          description: "",
          status: "success",
          duration: 3000,
        })
      });

      room.on('peerJoin', peerId => {
        console.log(peerId)
        toast.closeAll()
        toast({
          position: 'bottom',
          title: '',
          description: `ID: ${peerId}`,
          status: 'success',
          duration: 3000,
        })
      });

      room.on('peerLeave', (peerId) => {
        setRemoteVideo((prev) => {
          return prev.filter((video) => {
            if (video.peerId === peerId) {
              video.stream.getTracks().forEach((track) => track.stop());
            }
            return video.peerId !== peerId
          })
        })

        toast({
          position: 'bottom',
          title: '',
          status: "error",
          description: `ID: ${peerId}`,
          duration: 3000,
        })
      })

      room.on('stream', async (stream) => {
        setRemoteVideo((prev) => [
          ...prev,
          { stream: stream, peerId: stream.peerId },
        ]);
      })
    } catch (error) {
      console.log(error)
      toast.closeAll()
      toast({
        position: 'bottom',
        description: "",
        status: "error",
        duration: 3000,
      })
    }
  }

  useEffect(() => {
    setTimeout(onStartStream, 1000)

    toast({
      position: 'bottom',
      description: "...",
      duration: null,
    })
  }, [])

  const showCamera = () => {
    return remoteVideo.map((video) => {
      return <CameraCard video={video} key={video.peerId} />;
    });
  };

  const onCopyUrl = () => {
    onCopy()
    toast({
      position: 'bottom',
      description: '',
      status: 'success',
      duration: 3000,
    })
  }

  const onDropout = () => {
    if (window.confirm('')) {
      navigate('/')
      window.location.reload()
    }
  }

  return (
    <>
      <Header/>
        <Wrap justify={["center", "space-between"]} mr={5} ml={5}>
          <WrapItem>
            <Heading ml={5} mr={5} mt={5} size="md" color="gray.700"><Center></Center></Heading>
          </WrapItem >
          <WrapItem >
            <QRLinkModalButton/>
            <Menu>
              <MenuButton
                mt={2}
                mb={2}
                ml={2}
                mr={5}
                as={Button}
                variant="solid"
                colorScheme="blue"
                rightIcon={<ChevronDownIcon />}
              >
                 
              </MenuButton>
              <MenuList>
                <MenuItem onClick={onCopyUrl}><LinkIcon/></MenuItem>
                <MenuItem onClick={() => window.location.reload()}><RepeatIcon/></MenuItem>
                <Divider mt={2} mb={2}/>
                <MenuItem onClick={onDropout}><ArrowBackIcon/></MenuItem>
              </MenuList>
            </Menu>
          </WrapItem >
        </Wrap>
        <Box h="3px" m={2} bg="blue.400"/>
        <Wrap m={5}>
          {showCamera()}
        </Wrap>
      <Footer/>
    </>
  );
}

export default Dashboard;
  